<template>
  <el-row>
    <el-col>
      <h1 class="h1">Brokalys pingeris</h1>
      <p>
        Aizpildi formu un saem paziojumus e-past par jauniem nekustam
        pauma sludinjumiem.
      </p>

      <img src="./diagram.svg" alt="Diagram" />

      <el-divider />

      <el-form
        ref="form"
        :model="form"
        :rules="rules"
        label-position="left"
        :disabled="loading"
      >
        <el-form-item label="E-pasta adrese" prop="email">
          <el-col :span="11">
            <el-input placeholder="demo@brokalys.com" v-model="form.email">
            </el-input>
          </el-col>
        </el-form-item>

        <el-row>
          <el-col :span="11">
            <el-form-item label="Nekustam pauma tips" prop="category">
              <el-select v-model="form.category">
                <el-option label="Dzvoklis" value="APARTMENT"></el-option>
                <el-option label="Mja" value="HOUSE"></el-option>
                <el-option label="Zeme" value="LAND"></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :span="11" :offset="2">
            <el-form-item label="Darjuma veids" prop="type">
              <el-select v-model="form.type">
                <el-option label="Prdod" value="SELL"></el-option>
                <el-option label="r" value="RENT"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item label="Cena (no, ldz)" required>
          <el-col :span="11">
            <el-form-item prop="price_min">
              <el-input v-model.number="form.price_min" auto-complete="off">
                <template slot="append">EUR</template>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="price_max">
              <el-input v-model.number="form.price_max" auto-complete="off">
                <template slot="append">EUR</template>
              </el-input>
            </el-form-item>
          </el-col>
        </el-form-item>

        <el-form-item label="Istabas (no, ldz)">
          <el-col :span="11">
            <el-form-item prop="rooms_min">
              <el-input v-model.number="form.rooms_min" auto-complete="off">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="rooms_max">
              <el-input v-model.number="form.rooms_max" auto-complete="off">
              </el-input>
            </el-form-item>
          </el-col>
        </el-form-item>

        <el-form-item label="Platba (no, ldz)">
          <el-col :span="11">
            <el-form-item prop="area_m2_min">
              <el-input v-model.number="form.area_m2_min" auto-complete="off">
                <template slot="append">m2</template>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="area_m2_max">
              <el-input v-model.number="form.area_m2_max" auto-complete="off">
                <template slot="append">m2</template>
              </el-input>
            </el-form-item>
          </el-col>
        </el-form-item>

        <el-form-item label="Reions" required>
          <el-col>
            <GoogleMap v-bind:region.sync="region" />
          </el-col>
        </el-form-item>

        <el-form-item label="Komentri" prop="comments">
          <el-col>
            <el-input
              type="textarea"
              placeholder="Ieteikumi, atsauksmes"
              v-model="form.comments"
            >
            </el-input>
          </el-col>
        </el-form-item>

        <div
          v-if="freeLimit !== undefined"
          v-loading="$apollo.queries.freeLimit.loading"
        >
          <el-progress :percentage="freeLimit.percentage"></el-progress>
          <el-alert
            :title="selectedMonthlyEmailsTitle"
            type="info"
            :closable="false"
          ></el-alert>

          <template v-if="freeLimit.percentage >= 90">
            <el-alert
              :title="maxMonthlyEmailsTitle"
              :description="maxMonthlyEmailsDescription"
              type="warning"
              :closable="false"
            ></el-alert>
          </template>
        </div>

        <el-form-item>
          <el-button type="primary" @click="submitForm()" :loading="loading">
            Saemt nek.p. paziojumus
          </el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import { Message } from "element-ui";
import moment from "moment";
import bugsnagClient from "../bugsnag";
import GET_PINGER_STATS from "../graphql/GetPingerStats.gql";
import CREATE_PINGER from "../graphql/CreatePinger.gql";

const PINGER_LIMIT_PER_EMAIL = 100;

export default {
  name: "SignupForm",
  components: {
    GoogleMap: () => import("./GoogleMap.vue"),
  },

  apollo: {
    freeLimit: {
      query: GET_PINGER_STATS,
      variables() {
        const form = { ...this.form };
        delete form.email;

        return {
          ...form,
          region: this.region,
          published_at_start: moment()
            .utc()
            .startOf("month")
            .subtract(1, "month")
            .toISOString(),
          published_at_end: moment()
            .utc()
            .endOf("month")
            .subtract(1, "month")
            .toISOString(),
        };
      },
      debounce: 500,
      update(data) {
        if (!data.properties) return;

        const pingers = data.properties.summary.count;
        return {
          amount: pingers,
          percentage:
            pingers > PINGER_LIMIT_PER_EMAIL
              ? 100
              : (pingers / PINGER_LIMIT_PER_EMAIL) * 100,
        };
      },
      skip() {
        if (
          !this.form.category ||
          !this.form.type ||
          this.form.price_min === undefined ||
          this.form.price_max === undefined ||
          !this.region
        ) {
          return true;
        }

        return false;
      },
    },
  },

  data() {
    const greaterThan = (field) => (rule, value, callback) => {
      if (!value) {
        return callback();
      }

      if (this.form[field] >= value) {
        return callback(
          new Error("Lauciam /no/ ir jbt lielkam par lauciu /ldz/.")
        );
      }

      return callback();
    };

    return {
      loading: false,
      region: "",

      form: {
        email: "",
        category: "APARTMENT",
        type: "SELL",
        price_min: undefined,
        price_max: undefined,
        rooms_min: undefined,
        rooms_max: undefined,
        area_m2_min: undefined,
        area_m2_max: undefined,
        optin: false,
      },
      rules: {
        email: [
          {
            required: true,
            message: "is lauci ir obligti aizpildms.",
            trigger: "blur",
          },
          {
            type: "email",
            message: "Prbaudi e-pasta adresi.",
            trigger: "blur",
          },
        ],
        category: [
          {
            required: true,
            message: "is lauci ir obligti aizpildms.",
            trigger: "blur",
          },
        ],
        type: [
          {
            required: true,
            message: "is lauci ir obligti aizpildms.",
            trigger: "blur",
          },
        ],
        price_min: [
          {
            required: true,
            message: "is lauci ir obligti aizpildms.",
            trigger: "blur",
          },
          {
            type: "integer",
            message: "aj lauci var ievadt tikai skaitus.",
            trigger: "blur",
            min: 1,
          },
        ],
        price_max: [
          {
            required: true,
            message: "is lauci ir obligti aizpildms.",
            trigger: "blur",
          },
          {
            type: "integer",
            message: "aj lauci var ievadt tikai skaitus zem 10000000.",
            trigger: "blur",
            max: 10000000,
          },
          { validator: greaterThan("price_min"), trigger: "blur" },
        ],
        rooms_min: [
          {
            type: "integer",
            message: "aj lauci var ievadt tikai pozitvus skaitus.",
            trigger: "blur",
            min: 0,
          },
        ],
        rooms_max: [
          {
            type: "integer",
            message: "aj lauci var ievadt tikai skaitus lds 20.",
            trigger: "blur",
            max: 20,
          },
          { validator: greaterThan("rooms_min"), trigger: "blur" },
        ],
        area_m2_min: [
          {
            type: "integer",
            message: "aj lauci var ievadt tikai pozitvus skaitus.",
            trigger: "blur",
            min: 0,
          },
        ],
        area_m2_max: [
          {
            type: "integer",
            message: "aj lauci var ievadt tikai skaitus ldz 1000.",
            trigger: "blur",
            max: 1000,
          },
          { validator: greaterThan("area_m2_min"), trigger: "blur" },
        ],
      },
    };
  },

  computed: {
    selectedMonthlyEmailsTitle: (vm) => {
      if (!vm.freeLimit) return;
      return `Pc tevis izvltajiem kritrijiem - pagjuaj mnes atrasti ${vm.freeLimit.amount} sludinjumi, kas ir ${vm.freeLimit.percentage}% no bezmaksas limita.`;
    },
    maxMonthlyEmailsTitle: () =>
      `Maksimlais bezmaksas e-pastu skaits mnes: ${PINGER_LIMIT_PER_EMAIL}`,
    maxMonthlyEmailsDescription: () =>
      `Katru mnesi vairki simti lietotji saem bezmaksas e-pastus no Brokalys par aktulajiem nekustmajiem paumiem Latvij. Lai nodrointu tik pat augstu servisu k ldz im,  esam spiesti ierobeot nosttu e-pastu skaitu katram lietotjam. Citiem vrdiem sakot - mnea laik nesaemsi vairk par  ${PINGER_LIMIT_PER_EMAIL} e-pastiem.`,
  },

  methods: {
    submitForm() {
      this.$refs.form.validate((valid) => {
        if (valid === false) {
          return;
        }

        this.loading = true;
        this.$apollo
          .mutate({
            mutation: CREATE_PINGER,
            variables: {
              ...this.form,
              region: this.region,
            },
          })
          .then(() => {
            Message({
              message:
                "Turpmk e-past saemsi N paziojumus, kas atbilst tevis izvltajiem kritrijiem.",
              type: "success",
              duration: 20000,
            });
          })
          .catch((error) => {
            let errors;

            if (error.networkError) {
              errors = error.networkError.result.errors;
            } else {
              errors = error.graphQLErrors;
            }

            if (
              errors.length > 0 &&
              errors[0].extensions.exception &&
              errors[0].extensions.exception.maxPingers
            ) {
              const maxPingers = errors[0].extensions.exception.maxPingers;
              Message({
                message: `Dieml, vienai e-pasta adresei var pievienot tikai ${maxPingers} N paziojumus.`,
                type: "error",
                duration: 20000,
              });
              return;
            }

            throw error;
          })
          .catch((error) => {
            Message({
              message:
                "Oops, kaut kas nogja greizi. Centsimies atrisint problmu tuvkaj laik.",
              type: "error",
              duration: 20000,
            });

            bugsnagClient.addMetadata("Errors", { ...error });
            bugsnagClient.notify(
              "Unexpected error occurred when creating a new pinger."
            );
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
  },
};
</script>

<style>
.el-select,
.el-form-item__label {
  width: 100%;
}

.el-alert {
  margin: 20px 0;
}

img {
  width: 100%;
}
</style>
